<template>
<div class="login-page">
  <div class="container">
    <div class="max-w-sm mx-auto">
      <div class="app-logo flex justify-center pt-10">
        <img :src="require('@/assets/img/Lifeloop-logo.svg')" alt="">
      </div>
      <form class="app-form bg-white shadow-md rounded m-5 px-8 pt-6 pb-8 mb-4">
        <div class="mb-4">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="username">
            Email
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" v-model="email" type="text" placeholder="Email" required>
        </div>
        <div class="mb-6">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="password">
            Password
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" v-model="password" type="password" placeholder="***************" required>
          <!-- <p class="text-red-500 text-xs italic">Please choose a password.</p> -->
          <span class="text-red-500 text-xs italic pb-2" v-if="errorMessage">{{ errorMessage }}</span>
        </div>
        <div class="">
          <button @click.prevent="login" class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="submit">
            <span v-if="!status">Log In</span>
            <span v-else>Loading</span>
          </button>
          <!-- <a class="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-800" href="#">
            Forgot Password?
          </a> -->
        </div>
      </form>
      <p class="text-center text-white text-xs">
        &copy;2019 Vervespire Technologies Inc. All rights reserved.
      </p>
    </div>
  </div>
</div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      email: 'besabellacyrus@gmail.com',
      password: 'password',
      errorMessage: ''
    }
  },
  created () {
    if (!this.isLoggedIn) {
      this.$router.push('/login')
    } else {
      this.$router.push('/')
    }
  },
  computed: {
    ...mapGetters({
      status: 'authStatus',
      isLoggedIn: 'isLoggedIn'
    })
  },
  methods: {
    async login () {
      const payload = {
        email: this.email,
        password: this.password
      }
      if (this.email !== '' && this.password !== '') {
        this.$store.dispatch('login', payload).then(() => {
          this.$router.push('/')
        }).catch(err => console.log(err))
      } else {
        this.errorMessage = 'Please provide Email or Password'
      }
    }
  }
}
</script>
<style lang="scss">
.login-page {
  background-image: linear-gradient(40deg,#3ab54a,#7fc342);
  background-repeat: no-repeat;
  height: 100vh;
}
.app-form {
  margin-top: 10vh;
}
.app-logo {
  img {
    height: 10vh;
  }
}
</style>
